export default {
	data() {
		const currentDate = this.getDate({
			format: true
		})
		return {
			date: currentDate,
			array: ['09:00-11:30', '13:00-15:00', '15:30-17:30', '19:00-21:00'],
			index: 0,
			conferenceList:[],
			current:'1',
			flag:false,
			time:'09:00',
			endtime:'11:30',
			fulltime:'09:00-11:30',
			token:'',
			hasMe:true,
			number:'',
			pageIndex:0,
			isOver:false
		}
	},
	onShow() {
		this.getToken()
		
	},
	onReachBottom() {
		var that = this
		if(this.conferenceList.length < this.pageIndex*5){
			this.isOver = true
		}
		console.log(this.conferenceList.length)
		this.pageIndex++
		this.getconferenceList()
	},
	//时间选择
	computed: {
		startDate() {
			return this.getDate('start');
		},
		endDate() {
			return this.getDate('end');
		}
	    },
	methods: {
		//获取token
		getToken(){
			var that = this
			uni.getStorage({
				key:'token',	
				success: function (res) {
				that.token = res.data.token
				that.getconferenceList()
				},
			})
		},
		// 获取空闲会议室列表
		getconferenceList(){
			var that = this;
			var page = this.current -1
			var startTime = this.date+" "+this.time
			uni.request({
				url:'http://112.74.86.165:8082/meetingRoomSpare',
				method:'GET',
				data:{
					pagination:this.pageIndex,
					size:'5',
					startTime:startTime
				},
				header:{
					Authorization:that.token
				},
				success(res) {
					console.log(res)
					that.conferenceList = [...that.conferenceList,...res.data.data.list]
					if(that.conferenceList.length > 5){
						that.flag = true
					}else if (that.conferenceList.length == 0){
						that.hasMe = false
					}
				}
			})
		},
		//分页
		pagination:function(e){
			console.log(e)
		},
		//日期选择
		bindDateChange: function(e) {
			this.date = e.target.value
			console.log(this.date)
			
		},
		//取消日期选择
		cancelDateChange:function(e){
			console.log(e)
		},
		//时间段选择
		bindPickerChange: function(e) {
			this.index = e.target.value
			if(this.index == 0){
				this.time = '09:00'
				this.endtime = '11:30'
				this.fulltime = '09:00-11:30'
			}
			else if(this.index == 1){
				this.time = '13:00'
				this.endtime = '15:00'
				this.fulltime = '13:00-15:00'
			}
			else if(this.index == 2){
				this.time = '15:30'
				this.endtime = '17:30'
				this.fulltime = '15:30-17:30'
			}else 
			{
				this.time = '19:00'
				this.endtime = '21:00'
				this.fulltime = '19:00-21:00'
			}
			
		},
		//日期格式
		getDate(type) {
			const date = new Date();
			let year = date.getFullYear();
			let month = date.getMonth() + 1;
			let day = date.getDate();
			if (type === 'start') {
				year = year - 60;
			} else if (type === 'end') {
				year = year + 2;
			}
			month = month > 9 ? month : '0' + month;;
			day = day > 9 ? day : '0' + day;
			return `${year}-${month}-${day}`;
		},
		//预约会议室
		toAppointment(id,number){
			var fullTime = this.date+" "+this.fulltime
			var time = this.time
			var endtime = this.endtime
			var date = this.date
			uni.navigateTo({	
				url:'../appointment/appointment?id='+id+'&'+'fulltime='+fullTime+'&'+'time='+time+'&'+'endtime='+endtime+'&'+'date='+date+'&'+'number='+number,
			})
		}
	}
}
